.main-aside{
    a, p, h1, h2, h3, h4, h5, h6, button, li{
        font-family: Blabeloo;
    }
    position: fixed;
    top: 0;
    right: 0;
    width: 70px;
    height: 100%;
    background: $pr-color;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    box-shadow: 0px -1px 10px rgb(50, 50, 50);


    header{
        position: absolute;
        top: .1vh;
        right: 35px;
        transform: translateX(50%);

        h1, p{
            color: white;
            text-align: center;
        }
        h1{
            font-size: 4rem;
        }
        p{
            margin-top: -19px;
            font-size: 1.2rem;
            line-height: 12px;
        }

    }

    .main-nav{
        width: auto;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        margin-top: 8vh;

        button{
            padding: 5px 0;
            margin: 2.5vh 0;
            text-align: center;
            color: white;
            position: relative;
            width: 50px;
            background-color: unset;
            border: none;
        }



        .active::after{
            content: ' ';
            // background-color: #EBE9E9;
            background: linear-gradient(90deg, rgba(0,0,0,0) 75%, rgba(235,233,233,1) 75%);

            position: absolute;
            left: -25px;
            top: 50%;
            transform: translateY(-50%);
            width: 20px;
            height: 20px;
            border-radius: 50%; 
        }
    }
}