$pr-color:#B53E4B;
$sc-color:#035e97;
$tr-color:#D09027;
$black:rgb(40, 40, 40);
$bg:#EBE9E9;

@import "./fonts/font.scss";

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    direction: rtl;

}
body{
    background-color: $bg;
}
ul{
    list-style: none;
}
a, p, h1, h2, h3, h4, h5, h6{
    text-decoration: none;
    color: $black;
}
img{
    width: 100%;
    height: auto;
    object-fit: cover;
}

.btn{
    border: none;
    background: none;
    padding: 10px;
    border-radius: 10px;
    font-size: 1.1em;
    font-weight: bold;
    box-shadow: 1px 1px 5px rgb(49, 49, 49);
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.pr-btn{
    background-color: $sc-color;
    color: white;
}
.sc-btn{
    background-color: $bg;
    border: 1px solid $sc-color;
    color: $sc-color;

}


@import "./home.scss";
@import "./nav.scss";
@import "./menu.scss";
@import "./itemCard.scss";