a, p, h1, h2, h3, h4, h5, h6, button, li{
    font-family: "IranSans";
}

.hero{
    position: relative;
    height: 100vh;
    header{
        position: absolute;
        top: 20px;
        left: 0;
        width: 100%;
        text-align: center;

        h1, p {
            font-family: Blabeloo !important;
            color: $tr-color;
            font-size: 3em;
            line-height: 40px;
        }
    }

    .bg{
        height: 100%;
    }
    .cup{
        width: 90vw;
        position: absolute;

        @media screen and (min-width:425px) {
            width: 400px;
        }
    }

    .home-nav{
        position: absolute;
        bottom: 50px;
        left: 0;
        width: 100%;
        text-align: center;
        display: grid;
        grid-template-columns: 130px 130px;
        justify-content: center;
        gap: 10px;

    }
}

.home-view{

    .about, .address, .prv-room{
        padding: 50px 5vw;

        .head{
            display: flex;
            align-items: center;
            h1{
                background-color: $sc-color;
                padding: 5px;
                margin-left: 10px;
                border-radius: 10px;
                white-space: nowrap;
                color: white;
            }
            hr{
                height: 5px;
                width: 100%;
                border: 5px solid $sc-color;

            }
        }

        .info{
            .title{
                font-size: 1.2em;
                padding: 10px;
                border-radius: 10px;
                color: white;
                width: 100%;
                margin: 10px 0;
                text-align: center;

                @media screen and (min-width:768px) {
                    width: max-content;
                }
            }
        }
    }
    
    .about{

        .info{
            display: grid;
            grid-template-columns: 1fr;
            gap: 30px;
            align-items: center;
           @media screen and (min-width:768px) {
            grid-template-columns: 1fr 1fr;
            }

            .description{
                .title{
                    display: block;
                    font-size: 1.8em;
                    background-color: $pr-color;
                    width: max-content;
                }
            }


            .imgs {
                display: grid;
                grid-template-columns: repeat(7, 1fr);
                grid-template-rows: repeat(8, 1fr);
                gap: 10px;

                img{
                    width: 100%;
                    border: 5px solid $sc-color;
                }

                .img1 { grid-area: 1 / 5 / 5 / 8; }
                .img2 { grid-area: 5 / 4 / 9 / 7; }
                .img3 { grid-area: 2 / 1 / 8 / 4; }
            }

        }

    }

    .prv-room{
        position: relative;
        background-color: white;
        
        .head{
            position: relative;

            .birth1{
                width: 80vw;
                overflow: hidden;
            }
            .birth3{
                position: absolute;
                top: -140px;
                right: 0;
                width: 130px;
            }
        }

        .info{
            margin-top: 20px;
            display: grid;
            grid-template-columns:1fr;
            gap: 30px;
            align-items: center;
            margin-bottom: 30px;

            @media screen and (min-width:768px) {
                grid-template-columns: .5fr 1fr;
            }

            .right{
                width: 100%;
                display: flex;
                flex-direction: column;
                justify-content: space-between;

                .title{
                    background-color: $tr-color;
                }

                .description{
                    margin: 20px 0;
                }
    
                .imgs{
                    gap: 30px;
                    display: grid;
                    grid-template-columns: repeat(1, 1fr);

                    @media screen and (min-width:768px) {
                        grid-template-columns: repeat(3, 1fr);
                    }
                }
            }
        }
    }

    .address{

        .info{
            display: grid;
            grid-template-columns: 1fr;
            
            @media screen and (min-width:768px) {
                grid-template-columns: 1fr 1fr;
            }

            .contact{
                margin: 20px 0;
                display: flex;
                flex-direction: column;
                h1{
                    display: block;
                    background-color: $pr-color;
                    padding: 10px;
                    border-radius: 10px;
                    color: white;
                    width: max-content;
                    margin: 10px 0;
                }

                ul{
                    li{
                        margin: 10px 0;
                        .key{
                            font-weight: bold;
                            i{
                                color: $pr-color;
                            }
                        }
                    }
                }
                .instagram{
                    margin: 20px 0;
                    align-self: center;
                    width: 230px;
                    background-color: $tr-color;
                }
            }

            .map{
                @media screen and (max-width:768px) {
                    width: 100%;
                    height: 300px;
                }
                iframe{
                    border: none;
                }
            }
        }
    }
}

footer{
    background-color: $pr-color;
    width: 100%;
    padding: 10px 0;

    p{
        color: white;
        text-align: center;
        @media screen and (max-width: 420px) {
            font-size: .8em;
        }
    }
}