.itemCard{
    width: 90%;
    height: 130px;
    max-height: auto;



    background-color: white;
    border-radius: 30px;

    padding: 20px;


    position: relative;


    .card-title{
        width: 70%;
        font-size: 1em;

        @media only screen and (min-width: 480px){
            font-size: 1.2em;

        }

    }

    .card-price{
        font-size: 1rem;
        margin-top: 10px;

    }


    .card-img{
        width: 80px;
        height: auto;
        // float: left;
        // margin-top: 35px;


        position: absolute;
        bottom: -20px;
        left: -10px;
    }


    .card-buttom{
        position: absolute;
        bottom: -10px;
        right: 0px;

        width: 40px;
        height: 40px;
        border-radius: 50%;
        border: 5px solid #EBE9E9;
        // font-size: 5rem;
        background-color: $tr-color;

        cursor: pointer;

        i{
            font-size: 1.5em;
            margin-top: 5px;
            color: white;
        }

        transition: all .5s;

    }


    .materials{
        display: none;
    }


    transition: all .5s;

}

.open{
    .card-buttom{
        transform: rotate(180deg);
        transition: all .5s;
    }


    .materials{
        margin: 15px 0;
        display: grid;
        grid-template-columns: auto auto;
        width: 80%;
        gap: 5px;
        li{
            font-family: Anil;
        }
    }


    height: auto;
    max-height: auto;
    transition: all .5s;

}