
.menu-view{
    float: left;
    width: calc(100vw - 70px);
    padding: 10px;
    background-color: #EBE9E9;
    min-height: 100vh;
    a, p, h1, h2, h3, h4, h5, h6, button, li{
        font-family: Blabeloo;
    }

    hr{
        height: 5px;
        background-color: $sc-color;
        border: none;
    }
    
    .menu-header{
        margin: 10px;
    }

    .subCat{
        margin-top: 10px;
        display: flex;
        width: 100%;
        overflow-y: scroll;
        &::-webkit-scrollbar {
            display: none;
        }
        
        button{
            padding: 10px;
            margin-left: 5px;
            font-size: .8rem;
            white-space: nowrap;

            border: 1px solid $pr-color;
            border-radius: 10px;

            cursor: pointer;
        }

        .active{
            background-color: $pr-color;
            color: white;
        }
    }


    .cardList{
        padding: 30px 0;
        display: grid;
        grid-template-columns: 1fr;
        gap: 50px;
        justify-items: center;

        @media only screen and (min-width: 768px){
            grid-template-columns: 1fr 1fr;

        }
        @media only screen and (min-width: 1024px){
            grid-template-columns: 1fr 1fr 1fr;

        }
    }
}